.App, #root {
    height: 100%;
    width: 100%;
}

body, code, html, kbd, pre, samp {
    font-family: Press Start\ 2P !important;
}

.caption {
    font-size: 6pt;
}

.login-container {
    height: 100%;
}

.login-container-loading {
    background-color: rgba(0, 0, 0, 0.3);
}

.nes-input, .nes-textarea {
    -moz-box-shadow: 0 4px #212529, 0 -4px #212529, 4px 0 #212529, -4px 0 #212529;
    -webkit-box-shadow: 0 4px #212529, 0 -4px #212529, 4px 0 #212529, -4px 0 #212529;
    box-shadow: 0 4px #212529, 0 -4px #212529, 4px 0 #212529, -4px 0 #212529;
    -webkit-appearance: none;
}
.overlay {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 10;
    background-color: rgba(0, 0, 0, 0.5); /*dim the background*/
}


.wrapper {
    text-align: center;
}

.loading-container {
    margin: 0;
    position: absolute !important;
    top: 50%;
    left: 50%;
    -ms-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);

    width: 90%;
    max-width: 200px;

    background-color: white;
    text-align: center;

    z-index: 15;
}

.message {
    width: 265px;
    position: absolute !important;
    bottom: 5px;
    right: 20px;
    background-color: lightcoral !important;
    display: flex !important;
}

.infoMessage {
    width: 265px;
    position: absolute !important;
    bottom: 5px;
    right: 20px;
    display: flex !important;
    background-color: #f2c409 !important;


}

.nes-table {
    font-size: 7pt;
    width: calc(100% - 8px);
    text-align: center;
}

.table {
    height: calc(100% - 162px);
    margin-bottom: 10px;
    overflow-y: auto !important;
}

.hidden {
    display: none !important;
}

.nes-container.with-title.results img {
    margin-top: 10px;
}
.results_buttons button {
    margin: 10px;
}

.nes-container.with-title > .title {
    margin: -2.2rem 0 1rem;
}

/* Portrait 6s */
@media only screen
and (min-device-width: 375px)
and (max-device-width: 667px)
and (-webkit-min-device-pixel-ratio: 2)
and (orientation: portrait) {
    * {
        font-size: 8pt;
    }

    .nes-container.with-title {
        padding: 2rem 60px 60px;

    }

    .nes-container.with-title.results {
        padding: 2rem 30px 30px;
    }


    .nes-container.with-title.uni {
        margin-top: 10px;
        height: 90px;
        margin-bottom: 20px;
        padding: 2rem;
    }
    .nes-container.with-title > .title {
        margin: -1.9rem 0 1rem !important;
    }
}

input[type="color"],
input[type="date"],
input[type="datetime"],
input[type="datetime-local"],
input[type="email"],
input[type="month"],
input[type="number"],
input[type="password"],
input[type="search"],
input[type="tel"],
input[type="text"],
input[type="time"],
input[type="url"],
input[type="week"],
select:focus,
textarea {
    font-size: 16px;
}

/* Portrait SE */
@media screen and (device-aspect-ratio: 40/71) {
    * {
        font-size: 8pt;
    }

    .nes-container.with-title {
        padding: 25px;
    }

    .uni {
        margin-top: 10px;
        height: 90px;
    }

}
